		.content-wrap{
			&__outer{}
			&__inner{}
			.withparents{
				&__area{}
				&__box{}
				&__bg{}
				&__items{
					&.length5{
						.withparents__item{
							&.item-vl{top: 141px;left: 216px;}
							&.item-sc{top: 141px;left: 540px;}
						}
					}
				}
				&__item{/*transition: background-image 0.3s;*/
					$lists: (
						mg: (title: mg, posX: 90px, posY: 133px, w: 253px, h: 240px),
						vl: (title: vl, posX: 383px, posY: 155px, w: 276px, h: 241px),
						sc: (title: sc, posX: 691px, posY: 141px, w: 263px, h: 233px),
						ts: (title: ts, posX: 109px, posY: 420px, w: 248px, h: 246px),
						pr: (title: pr, posX: 397px, posY: 451px, w: 248px, h: 240px),
						re: (title: re, posX: 680px, posY: 416px, w: 248px, h: 247px)
					);
					@each $author, $map in $lists {
						&.item-#{map-get($map, title)} {position: absolute;top: #{map-get($map, posY)};left: #{map-get($map, posX)};width: #{map-get($map, w)};height: #{map-get($map, h)};background: no-repeat 50% 50%;
							&.kor{background-image: url("/assets/images/with_parents/img_kor_#{$author}01.png");
								&.active{background-image: url("/assets/images/with_parents/img_kor_#{$author}01_active.png");}
							}
							&.eng{background-image: url("/assets/images/with_parents/img_eng_#{$author}01.png");
								&.active{background-image: url("/assets/images/with_parents/img_eng_#{$author}01_active.png");}
							}
						}
					}
				}
			}
			.btn{display: block;width: 100%;height: 100%;}
